      <li v-for="publication in publications" :key="publication.name">
        <div class="project-item">
          <div class="project-info">
            <h3>{{ publication.name }}</h3>
            <h5>Presentation on {{ publication.position }}</h5>
            <div class="tags">
              <div class="tag" v-for="item in publication.skills" :key="item">{{ item }}</div>
            </div>
          </div>
          <div class="project-info">
            <h4>Description:</h4>
            <ul class="vertical-list">
              <div class="animated" v-for="(desc, index) in publication.descript" :key="index">{{ desc }}</div>
              <a class="animated" :href="publication.link" target="_blank" rel="noopener" style="padding:10px"> ➥ Presentation Detail</a>
            </ul>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import ScrollReveal from 'scrollreveal'

export default {
  name: 'MyPublications',
  data() {
    return {
      isMobile: false,
      windowWidth: window.innerWidth,
      publications: [
        {
          "name": "To Improve the Accuracy of Quantitative Metrics for Polystyrene (PS) Crack Patterns Using CNN-Based Deep Learning Models",
          "skills": ["Pytorch"],
          "position": "2024 MRS Fall Meeting & Exhibit, Boston, Massachusetts. (oral presentation)",
          "descript": [
            "Utilized computer vision to analyze material cracks, achieving IoU > 0.5 and improving identification accuracy by 16% through advanced preprocessing techniques.",
            "Fine-tuned a pre-trained model, resulting in an additional 1% improvement in IoU compared to the baseline pre-trained model."
          ],
          "link": "https://www.mrs.org/meetings-events/annual-meetings/archive/meeting/presentations/view/2024-fall-meeting/2024-fall-meeting-4152159"
        }
      ]
    };
  },
  mounted() {
    ScrollReveal().reveal('.animated', {
      delay: 300,
      duration: 1000,
      origin: 'bottom',
      distance: '20px',
      easing: 'ease-in-out',
      reset: true
    });
    this.checkWidth();
    window.addEventListener('resize', this.checkWidth);
  },
  methods: {
    checkWidth() {
      this.isMobile = window.innerWidth <= 1000;
      this.windowWidth = window.innerWidth;
    }
  },
    beforeUnmount() {
        window.removeEventListener('resize', this.checkWidth);
    }
}
</script>


